import React from "react";
import { Row, Col } from "antd";
import ReactPlayer from 'react-player';

const Quality = () => {
  return (
    <div className="quality-row">
      <Row>
        <Col span={12} className="quality-col">
          <h1>UNCOMPROMISED QUALITY. RELIABLE RESULTS. ALWAYS.</h1>
          <p>Spending hours in a queue at a diagnostic lab or a hospital is now a thing of the past. 
            With 1mg you can get your lab tests done from the comfort of your home. Our highly qualified phlebotomist will 
            collect the samples at your preferred time. Watch this video to see steps taken by us to ensure quality and hygiene in 
            sample collection. Book your next lab test with 1mg at the lowest price guaranteed!</p>
        </Col>
        <Col span={12} className="quality-col">
              <ReactPlayer url='https://youtu.be/jo5gQxB0DlE' />
        </Col>
      </Row>
    </div>
  );
};

export default Quality;
