import React from "react";
import { Row, Col, Menu } from "antd";
import { ShoppingCartOutlined } from '@ant-design/icons';

function Topheader() {
  return (
    <div>
      <Row className='heading'>
        <Col span={3}>
          <img
            src="https://res.cloudinary.com/du8msdgbj/image/upload/v1571306929/logo_k1p18f.svg"
            alt="1mg, best e pharmacy in India"
            title="1mg, India's Largest Healthcare Platform"
            width="40px"
            height="20px"
          />
        </Col>
        <Col span={15}>
          <Menu mode="horizontal" defaultSelectedKeys={["2"]}>
            <Menu.Item key="1">Medicines</Menu.Item>
            <Menu.Item key="2">Lab Tests</Menu.Item>
            <Menu.Item key="3">Ask Doctor</Menu.Item>
            <Menu.Item key="4">Vaccine</Menu.Item>
            <Menu.Item key="5">Covid-19</Menu.Item>
            <Menu.Item key="6">Ayurveda</Menu.Item>
          </Menu>
        </Col>
        <Col span={5}>
        <span className='logintext'>Login | Sign Up</span>
        <span className='logintext'><ShoppingCartOutlined /></span>
        <span className='logintext'> Need Help</span>
        </Col>
      </Row>
    </div>
  );
}

export default Topheader;
